import React, { ReactNodeArray, cloneElement, ReactElement, useCallback, useState, RefObject, useRef, useMemo, useEffect } from 'react';
import styles from '../css/components.module.css';
import { FaSave } from 'react-icons/fa';
import { FiRefreshCcw } from 'react-icons/fi';

interface Props {
	className?: string
	onSubmit: (order: string[]) => void
	children: ReactElement[]
}

export default function Draggable(props: Props) {

	const [from, setFrom] = useState(-1);
	const [to, setTo] = useState(-1);
	const [dragHeight, setDragHeight] = useState(0);

	const [children, setChildren] = useState([]);
	const propsOrder: string[] = useMemo(() => props.children.map(elem => elem.props.id), [props.children]);
	const initialOrder: string[] = useMemo(() => children.map(elem => elem.props.id), [children]);
	const hasChanged: boolean = useMemo(() => {
		for (let i = 0; i < propsOrder.length; i++) {
			if (propsOrder[i] !== initialOrder[i]) return true;
		}
		return false;
	}, [propsOrder, initialOrder]);

	useEffect(() => {
		setChildren(props.children);
	}, [props.children]);

	const handleDrag = useCallback((e: React.DragEvent<HTMLDivElement>) => {
		let div = e.target as HTMLDivElement;
		if (!div) return;
		setFrom(initialOrder.indexOf(div.id));
		setDragHeight((e.target as HTMLDivElement).getBoundingClientRect().height);
	}, [initialOrder, setDragHeight, setFrom]);

	const handleDragOver = useCallback((e: React.DragEvent<HTMLDivElement>) => {
		let div = e.target as HTMLDivElement;
		if (div.id === 'last') setTo(initialOrder.length);
		else if (initialOrder.indexOf(div.id) === from + 1) setTo(-1);
		else if (initialOrder.indexOf(div.id) === from) setTo(-1);
		else setTo(initialOrder.indexOf(div.id));
	}, [initialOrder, dragHeight, setTo, from]);

	const handleDragEnd = useCallback((e: React.DragEvent<HTMLDivElement>) => {
		if (from < 0) return;
		if (to < 0) return;
		let fromValue = children[from];
		let toValue = children[to];
		let newChildren = children.slice(0);
		newChildren.splice(from, 1);
		newChildren.splice(to === children.length ? newChildren.length : newChildren.indexOf(toValue), 0, fromValue);
		setChildren(newChildren);
		setTo(-1);
		setFrom(-1);
	}, [setTo, setFrom, from, to, children, setChildren]);

	const handleReset = useCallback(() => {
		setChildren(props.children);
	}, [setChildren, props.children]);

	const handleSubmit = useCallback(() => {
		props.onSubmit(initialOrder);
	}, [props.onSubmit, initialOrder]);

	return <div className={[props.className].join(' ')}>
		{children.map((elem) => {
			return <div
				id={elem.props.id}
				className={styles.listElement}
				draggable
				onDragStart={handleDrag}
				onDragEnd={handleDragEnd}
				onDragOver={handleDragOver}
				style={{
					marginTop: to === initialOrder.indexOf(elem.props.id) && dragHeight ? dragHeight + 10 + 'px' : 0
				}}
			>
				<div className={[styles.number, 'fieldBox'].join(' ')}>#{propsOrder.indexOf(elem.props.id) + 1}</div>
				{React.cloneElement(elem, {
				})}
			</div>
		})}
		<div className={styles.buttonContainer}
			onDragOver={handleDragOver}
			style={{
				marginTop: to === children.length && dragHeight ? dragHeight + 10 + 'px' : 0
			}}
		>
			<div className={['fieldBox', styles.tbReset].join(' ')}
				id='last' 
				onDragOver={handleDragOver}
				onClick={handleReset}
				style={{
					opacity: hasChanged ? 1 : 0
				}}
			>
				<FiRefreshCcw />RESET
			</div>
			<div className={['fieldBox', styles.tbSubmit].join(' ')}
				id='last'
				onClick={handleSubmit}
				style={{
					visibility: hasChanged ? 'visible' : 'hidden'
				}}
			>
				<FaSave />SAVE
			</div>
		</div>
		<div>{'\u200b'}</div>
	</div>
}